  /* height: 100vh; */
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-weight: 400;
  background-color: var(--grey1);
  h1 {
    text-align: center;
  }
  label {
    display: block;
    margin-bottom: 0.25rem;
  }
}
.errorMsg {
  color: red;
}
#emailInput:valid {
  border: 2px solid green;
}
#emailInput:invalid {
  border: 2px solid red;
}
.noSelection {
  border: 2px solid red;
